body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.nav-wrapper {
  display: -webkit-flex;
  display: flex;
  padding-left: 10px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header-title {
  margin: 12px;
  font-size: 45px;
  /* padding-bottom: 55px; */
}

.nav-elements {
  padding: 10px 10px 10px 10px;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-weight: bolder;
  
}

.header-link {
  color: white;
  font-size: 25px;
  text-decoration: none;
}

.header-link:hover {
  color: rgba(255, 255, 255, .5)
}
.form-wrapper {
  max-width: 25%;
  margin-top: 50px;
  margin-left: 35px;
  padding: 5px;
  border-radius: 5px;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 10px;
  padding: 5px;
  box-shadow: 1px 1px 30px 1px rgb(211, 211, 211)
}

.input-field {
  padding: 15px;
  margin: 5px;
  border: none;
  border-radius: 50px;
  background-color: rgba(211, 211, 211, 0.5);
  border: 1px solid rgba(211, 211, 211, 0.0)
}

.input-field:focus {
  background-color: white;
  outline: none;
  border: 1px solid rgba(211, 211, 211, 0.5)
}

button {
  width: 35%;
  -webkit-align-self: center;
          align-self: center;
  margin: 5px;
  font-size: 20px;
  background-color: rgb(24, 159, 196);
  color: white;
  border-radius: 50px;
}
.charityList {
  margin: 35px;
}

li {
  list-style-type: none;
}
.charity-card {
  max-width: 35%;
  margin: 25px;
  padding: 25px;
  box-shadow: 1px 1px 25px 1px rgb(211, 211, 211);
  border-radius: 10px;
}

.charity-title {
  font-size: 35px;

}

.logo {
  max-width: 50%
}

.link {
  font-weight: bold;
  text-decoration: none;
}
.App-header {
  /* height: 180px; */
  background-color: rgb(24, 159, 196);
}

